.form {
  padding-left: 20px;
  padding-right: 20px;
}

.form__fieldset {
  border: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  margin-bottom: 64px;
}

.form__legend {
  text-align: center;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.form__list {
  @include list-style;
  display: flex;
  flex-direction: column;
  padding: 0;
}

.form__user-data {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
}

.form__list-impression {
  @include list-style;
}

.form__list-checkbox {
  @include list-style;
}
